<template>
  <div class="hello">
    <div>
      <el-row>
        <el-col :span="24">
          <div class="grid-content bg-purple-dark">
            <h1>{{ msg }}</h1>
          </div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="14">
          <div class="grid-content bg-purple" style="text-align:right;">
            <h1>{{result}}</h1>
          </div>
        </el-col>
        <el-col :span="10">
          <div class="grid-content bg-purple" style="text-align:left;margin:16px;">
            <el-button
              type="primary"
              v-clipboard:copy="result"
              v-clipboard:success="onCopy"
            >复制</el-button>
            <el-button type="warning" @click="getRandomArrayElements">刷新</el-button>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "随机密码",
      zhi: [
        "_",
        ".",
        "!",
        "@",
        "#",
        "$",
        "%",
        "^",
        "&",
        "*",
        "1",
        "2",
        "3",
        "4",
        "5",
        "6",
        "7",
        "8",
        "9",
        "0",
        "Q",
        "W",
        "E",
        "R",
        "T",
        "Y",
        "U",
        "I",
        "O",
        "P",
        "A",
        "S",
        "D",
        "F",
        "G",
        "H",
        "J",
        "K",
        "L",
        "Z",
        "X",
        "C",
        "V",
        "B",
        "N",
        "M",
        "q",
        "w",
        "e",
        "r",
        "t",
        "y",
        "u",
        "i",
        "o",
        "p",
        "a",
        "s",
        "d",
        "f",
        "g",
        "h",
        "j",
        "k",
        "l",
        "z",
        "x",
        "c",
        "v",
        "b",
        "n",
        "m"
      ],
      result:""
    };
  },
  mounted() {
   this.getRandomArrayElements();
  },
  methods: {
    onCopy(){
        this.$notify({
          title: '成功',
          message: '复制成功！',
          type: 'success'
        });
    },
    getRandomArrayElements() {
      var count = 16;
      var shuffled = this.zhi.slice(0),
        i = this.zhi.length,
        min = i - count,
        temp,
        index;
      while (i-- > min) {
        index = Math.floor((i + 1) * Math.random());
        temp = shuffled[index];
        shuffled[index] = shuffled[i];
        shuffled[i] = temp;
      }
      var str = shuffled.slice(min).toString();
      console.log(str);
      let reg = new RegExp(",", "g");
      console.log(str.replace(reg, ""));
      this.result=str.replace(reg, "");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



